<template>
    <glui-pic ref="picture" pic-win-width="1090px" picwidth="200px" picheight="150px" :isvideo="queryForm.PSType==1" :maxvideo="maxvideo" :picselect.sync="picselect" :delpic="remove" :glclass="'el-upload-find'" :picmaintab="picmaintab" :handel="handelc" :browse="true" :maxpic="maxpic" v-on:search-pic="searchPicture" :page-size="pageSize" :total-page="totalPage">
        <glui-form slot="picform" slot-scope="props" label-width="100px">
            <glui-row>
                <glui-col :span="8">
                    <glui-form-item label="">
                        <glui-select v-model="queryForm.PSCityId" filterable remote :remote-method="searchCity" clearable placeholder="">
                            <glui-option v-for="item in citySource" :key="item.id" :label="item.fullname" :value="item.id">
                            </glui-option>
                        </glui-select>
                    </glui-form-item>
                </glui-col>
                <glui-col :span="8">
                    <glui-form-item label="">
                        <glui-select v-model="queryForm.PSFromPlat" clearable placeholder="">
                            <glui-option v-for="item in fromPlats" :key="item.Id" :label="item.Name" :value="item.Id">
                            </glui-option>
                        </glui-select>
                    </glui-form-item>
                </glui-col>
                <glui-col :span="8">
                    <glui-form-item label="">
                        <glui-select :disabled="imagetypedisabled" v-model="queryForm.PSType" clearable placeholder="">
                            <glui-option label="" :value="0">
                            </glui-option>
                            <glui-option label="" :value="1">
                            </glui-option>
                        </glui-select>
                    </glui-form-item>
                </glui-col>
            </glui-row>
            <glui-row>
                <glui-col :span="8" v-if="Belong!=1">
                    <glui-form-item label="">
                        <glui-select v-model="queryForm.PSScenicId" clearable filterable remote :remote-method="searchScenic" placeholder="">
                            <glui-option v-for="item in scenerySource" :key="item.SceneryId" :label="item.SceneryName" :value="item.SceneryId">
                            </glui-option>
                        </glui-select>
                    </glui-form-item>
                </glui-col>
                <glui-col :span="8">
                    <glui-form-item label="">
                        <glui-input v-model="queryForm.PSName" placeholder=""></glui-input>
                    </glui-form-item>
                </glui-col>
                <glui-col :span="8">
                    <glui-form-item label=" ">
                        <glui-button type="success" size="small" plain v-on:click="upload"></glui-button>
                        <glui-button type="warning" size="small" v-on:click="search" plain></glui-button>
                        <glui-button type="primary" size="small" v-on:click="confirm(props.usepic)" plain></glui-button>
                    </glui-form-item>
                </glui-col>
            </glui-row>
        </glui-form>
    </glui-pic>
</template>
<script>

import httputils from "@/js/httputils"
export default {
    data: function () {
        return {
            fromPlats: [{
                Name: "",
                Id: 0
            },
            {
                Name: "",
                Id: 1
            },
                //{ Name: "", Id: 2 },
                //{ Name: "", Id: 3 },
                //{ Name: "", Id: 4 },
                //{ Name: "", Id: 5 },
                //{ Name: "", Id: -1 }
            ],
            maxpic: 9,
            maxvideo: 1,
            scenerySource: [],
            citySource: [],
            totalPage: 0,
            pageSize: 10,
            picselect: [],
            queryForm: {
                PSCityId: null,
                PSFromPlat: 0,
                PSName: null,
                PSScenicId: null,
                PSType: 0
            }
        };
    },
    props: {
        destcity: {
            type: [Array],
            default: function () {
                return [];
            }
        },
        value: {
            type: [Array],
            default: function () {
                return [];
            }
        },
        belong: {
            type: Number,
            default: function () {
                return 1;
            }
        },
        handelc: {
            type: Boolean,
            default: true
        },
        picmaintab: {
            type: Boolean,
            default: true
        },
        supplierid: {
            type: Number,
            default: 1
        },
        showvideo: {
            type: Boolean,
            default: false
        }
    },
    mounted() {
        this.picselect = this.value;
    },
    watch: {
        value: function (val) {
            this.picselect = val;
        },
        picselect: function (val) {
            this.$emit("input", val);
        },
        destcity: function (val) {
            var that = this;
            if (!val || val.length <= 0) {
                return;
            }
            httputils
                .get("/travel/supergoproduct/Common/GetCity", {
                    key: val.join(","),
                    isinner: false,
                    type: 1
                })
                .then(function (resp) {
                    if (resp) {
                        that.citySource = resp;
                    } else {
                        that.citySource = [];
                    }
                });
        }
    },
    computed: {
        imagetypedisabled: function () {
            if (this.belong == 1 && this.showvideo == true) {
                return false;
            }
            return true;
        }
    },
    methods: {
        searchCity: function (keyword) {
            var that = this;
            if (!keyword || keyword.length <= 0) {
                return;
            }
            httputils
                .get("/travel/supergoproduct/Common/GetCity", {
                    key: keyword,
                    isinner: false
                })
                .then(function (resp) {
                    if (resp) {
                        that.citySource = resp;
                    } else {
                        that.citySource = [];
                    }
                });
        },
        searchScenic: function (keyword) {
            var that = this;
            if (!keyword || keyword.length <= 0) {
                return;
            }
            httputils
                .get("/travel/supergoproduct/ProductBaseInfo/SearchSceneryInfo", {
                    k: keyword
                })
                .then(function (resp) {
                    if (resp) {
                        that.scenerySource = resp;
                    } else {
                        that.scenerySource = [];
                    }
                });
        },
        search: function () {
            this.$refs.picture.addPic();
        },
        upload: function () {
            var that = this;
            var owner = that.belong == "2" ? "3" : that.belong == "3" ? "2" : "1";
            window.open(
                "http://tcservice.17usoft.com/travel/crm/ImageUploadUnion/index?PSProjectOwner=" +
                owner
            );
        },
        remove(item) {
            this.picselect = this.picselect.filter(obj => {
                return item.picurl != obj.picurl;
            });
        },
        confirm: function (pics) {
            var that = this;
            var videolist = this.picselect.filter(item => {
                return item.isvideo == 1;
            });
            var piclist = this.picselect.filter(item => {
                return item.isvideo == 0;
            });
            if (piclist && piclist.length > that.maxpic) {
                that.$message({
                    message: "" + that.maxpic + "",
                    type: "error"
                });
                return;
            }
            if (videolist && videolist.length > that.maxvideo) {
                that.$message({
                    message: "" + that.maxvideo + "",
                    type: "error"
                });
                return;
            }
            if (pics && pics.length >= 1 && pics.findIndex(function (i) {
                return i.picmain == 1
            }) <= -1) {
                pics[0].picmain = 1;
            }
            that.picselect = pics;
            that.$refs.picture.closedig();
        },
        searchPicture: function (cb, page, pageSize) {
            var that = this;
            httputils
                .get("/travel/supergoproduct/Common/GetImages", {
                    PageIndex: page,
                    PageSize: pageSize,
                    PSCityId: that.queryForm.PSCityId,
                    PSProjectOwner: that.belong,
                    PSFromPlat: that.queryForm.PSFromPlat,
                    PSName: that.queryForm.PSName,
                    PSScenicId: that.queryForm.PSScenicId,
                    PSType: that.queryForm.PSType,
                    PSSupplierId: that.queryForm.PSFromPlat == 1 ? that.supplierid : ""
                })
                .then(function (resp) {
                    if (resp && resp.datas) {
                        that.totalPage = resp.total;
                        cb(resp.datas);
                    } else {
                        that.totalPage = 1;
                        cb([]);
                    }
                });
        }
    }
}
</script>



