<template>
    <glui-select v-model="selected" placeholder="" :multiple="multiple" :clearable="clearable" filterable remote :remote-method="search" v-on:change="onchange" v-on:clear="clear">
        <glui-option v-for="item in datas" :key="item.Value" :label="item.Value+'-'+item.Text" :value="item.Value">
            <span style="float: left">
                {{ item.Value+'-'+item.Text }}
            </span>
        </glui-option>
    </glui-select>
</template>
<script>
import httputils from "@/js/httputils";
export default {
    data: function () {
        return {
            datas: [],
            selected: this.value,
            selectdatas: []
        };
    },
    props: {
        props: {
            type: [Object],
            default() {
                return {
                    id: "Value",
                    name: "Text"
                }
            }
        },
        defaultitems: {
            default: function () {
                return null;
            },
            type: [Array]
        },
        value: {
            type: [String, Number, Array],
            default: function () {
                return "";
            }
        },
        belong: {
            type: [String, Number],
            default: function () {
                return 1;
            }
        },
        multiple: {
            type: Boolean,
            default: function () {
                return false;
            }
        },
        clearable: {
            type: Boolean,
            default: function () {
                return false;
            }
        }
    },
    watch: {
        defaultitems: function (val, old) {
            let that = this;
            if (val != old) {
                let datas = [];
                let selected = that.multiple ? [] : "";
                val && val.forEach(item => {
                    if (that.multiple) {
                        selected.push(item[that.props.id]);
                    } else {
                        selected = item[that.props.id];
                    }
                    datas.push({
                        Value: item[that.props.id],
                        Text: item[that.props.name]
                    });
                });
                that.datas = datas;
                that.selected = selected;
            }
        }
    },
    mounted: function () {
    },
    methods: {
        search: function (keyword) {
            var that = this;
            if (!keyword || keyword.length <= 0) {
                return;
            }
            httputils
                .get("/travel/supergoproduct/Common/GetProductManager", {
                    belong: that.belong,
                    keyword: keyword
                })
                .then(function (resp) {
                    if (resp) {
                        that.datas = resp;
                    } else {
                        that.datas = [];
                    }
                }).catch(err => {
                    that.$message({
                        type: 'error',
                        message: err
                    });
                });
        },
        onchange: function (val) {
            let that = this;
            that.$emit("input", val);
            let selected = that.datas.concat(that.selectdatas).filter(item => {
                if (that.multiple === true) {
                    return val.findIndex(v => v == item.Value) >= 0
                } else {
                    return item && item.Value == val;
                }
            });
            that.selectdatas = selected;
            that.$emit("change", val, selected && selected.length == 1 ? selected[0] : selected);
        },
        clear: function () {
            this.datas = [];
        }
    }
}
</script>




