<template>
    <glui-cascader :show-all-levels="false" :props="setprops" :options="datas" filterable :disabled="disabled" v-model="selected" :clearable="true" v-on:change="onchange" placeholder="">
    </glui-cascader>
</template>
<script>
import httputils from "@/js/httputils"
export default {
    data: function () {
        return {
            datas: [],
            selected: [],
            setprops: {
                children: "children",
                label: "label",
                value: "id",
                disabled: "disabled"
            }
        };
    },
    props: {
        value: {
            type: [String, Number],
            default: function () {
                return "";
            }
        },
        belong: {
            type: [String, Number],
            default: 1
        },
        producttype: {
            type: [String, Number],
            default: 1
        },
        disabled: {
            type: Boolean,
            default: function () {
                return false;
            }
        }
    },
    watch: {
        producttype: function (val) {
            this.initDatas();
        },
        belong: function (val) {
            this.initDatas();
        },
        value: function (val) {
            this.getSelected(this.datas);
        }
    },
    mounted: function () {
        this.initDatas();
    },
    methods: {
        initDatas: function () {
            var that = this;
            httputils
                .get("/travel/supergoproduct/SpecialType/GetSpecialTree", {
                    belong: that.belong,
                    producttype: that.producttype
                })
                .then(function (resp) {
                    if (resp && resp.Result) {
                        that.datas = resp.Result;
                        that.$nextTick(function () {
                            that.getSelected(that.datas);
                        });
                    } else {
                        that.datas = [];
                    }
                });
        },
        onchange: function (val) {
            this.$emit("input", val[val.length - 1]);
            this.$emit("onchange", val[val.length - 1]);
        },
        getSelected(children) {
            var that = this;
            if (children && children.length >= 1) {
                children.forEach(function (item) {
                    if (item.id == that.value) {
                        that.selected = item.levels;
                        return;
                    } else {
                        that.getSelected(item.children);
                    }
                });
            }
        }
    }
}
</script>




