import Vue from "vue";
import "./index.css"
import httputils from "@/js/httputils";
import dropdowncity from "@/components/dropdowncity";
import dropdownsupplier from "@/components/dropdownsupplier";
import dropdownmanagers from "@/components/dropdownmanagers";
import popuppicture from "@/components/popuppicture";
import dropdownlabels from "@/components/dropdownlabels";
import specraltree from "@/components/specraltree";
var vue = new Vue({
  el: "#container",
  components: {
    dropdowncity,
    dropdownsupplier,
    dropdownmanagers,
    popuppicture,
    dropdownlabels,
    specraltree
  },
  data: function () {
    var that = this;
    return {
      showbookcity: false,
      daysOption: Array.from({
        length: 61
      }, (v, k) => {
        var s = k;
        return {
          value: s,
          text: s
        };
      }),
      hoursOption: Array.from({
        length: 24
      }, (v, k) => {
        var s = k;
        return {
          value: s,
          text: s
        };
      }),
      productmanager: [],
      assistant: [],
      supplier: {
        SupplierId: "",
        SupplierName: ""
      },
      localSupplier: {
        SupplierId: "",
        SupplierName: ""
      },
      picselected: [],
      baseInfo: {
        PBIId: 0,
        PBIOldProductId: Number($("#productno").val()),
        PBIMainTitle: "",
        PBISubTitle: "",
        PBIProductManagerTcNo: null,
        PBIProductManagerName: "",
        PBIBelong: Number($("#belong").val()),
        PBICheckStaus: 0,
        PBIProductType: "",
        PBIProductPackageType: "",
        PBIProductOperateType: "",
        PBITripsTotal: "",
        PBIChannels: 0,
        PBIImageUrl: null,
        PBIScenicName: "",
        PBITrafficName: "",
        PBIPlayDayName: "",
        PBIProductAttribute: "",
        PBITagId: "0",
        PBITagName: null,
        PBIIncludeGuide: -1,
        PBIETemporary: 0,
        PBICaptionDescription: 1,
        PBIAssistantNo: null,
        PBIAssistant: "",
        PBISpecialTypeId: null,
        PBIEIsHoliday: 0,
        StopReceiveDays: 1,
        StopReceiveHours: 17,
        PSISupplierId: null,
        PSISupplierName: null,
        PSISupplierTitle: null,
        PSISupplierContractName: null,
        PSISupplierContractWay: null,
        PSILocalAgencySupplierId: null,
        PSILocalAgencySupplierName: "",
        Destination: [],
        Departure: "",
        RelationCity: [],
        Images: [],
        CountryGarden: 0,
        ExemptionRule: 0,
        ContainVisa: 0,
        PSILinkId: 0
      },
      destinationDatas: [],
      deptureDatas: [],
      bookDatas: [],
      borderTour: false,
      SupplierContract: [],
      rules: {
        PBIProductAttribute: [{
          required: true,
          message: "",
          trigger: "change"
        }],
        PBITripsTotal: [{
          required: true,
          message: "",
          trigger: "change"
        }],
        PBIProductOperateType: [{
          required: true,
          message: "",
          trigger: "change"
        }],
        PBIProductType: [{
          required: true,
          message: "",
          trigger: "change"
        }],
        PBIProductPackageType: [{
          required: true,
          message: "",
          trigger: "change"
        }],
        PSISupplierId: [{
          required: true,
          message: "",
          trigger: "change"
        }],
        PBISpecialTypeId: [{
          required: true,
          message: "",
          trigger: "change"
        }],
        PSISupplierContractName: [{
          required: true,
          message: ",EB",
          trigger: "blur"
        }],
        PBIAssistantNo: [{
          required: true,
          message: "",
          trigger: "change"
        }],
        PBIProductManagerTcNo: [{
          required: true,
          message: "",
          trigger: "change"
        }],
        Images: [{
          required: true,
          message: "",
          trigger: "blur"
        }],
        PSISupplierContractWay: [{
            required: true,
            message: ",EB",
            trigger: "blur"
          },
          //{ pattern: /^\s*\+?\s*(\(\s*\d+\s*\)|\d+)(\s*-?\s*(\(\s*\d+\s*\)|\s*\d+\s*))*\s*$/i, message: '', trigger: "blur" }
        ],
        PSISupplierTitle: [{
            validator: function (rule, value, callback) {
              if (that.baseInfo.PBIBelong == 2) {
                if (!value || value.length <= 0) {
                  return callback(new Error(""));
                }
              }
              callback();
            },
            trigger: "blur"
          },
          {
            min: 1,
            max: 100,
            message: "1-100",
            trigger: "blur"
          }
        ],
        PBIScenicName: [{
            validator: function (rule, value, callback) {
              if (!value) {
                return callback(new Error(""));
              }
              if (that.baseInfo.PBIBelong == 2) {
                var re = /^([\u4E00-\uFA29]|[\uE7C7-\uE7F3]|[a-zA-Z0-9+]){0,60}$/;
                if (!re.test(value)) {
                  return callback(new Error("+"));
                }
              } else if (that.baseInfo.PBIBelong == 1) {
                if (that.baseInfo.CountryGarden != 1) {
                  var re = /^([\u4E00-\uFA29]|[\uE7C7-\uE7F3]|[a-zA-Z0-9+-]){0,60}$/;
                  if (!re.test(value)) {
                    return callback(new Error("+-"));
                  }
                }
                $("||||".split("|")).each(function () {
                  var obj = this;
                  if (value.indexOf(obj) > -1)
                    return callback(new Error(obj + "/"))
                })
              }
              callback();
            },
            trigger: "blur"
          },
          {
            min: 1,
            max: 50,
            message: "1-50",
            trigger: "blur"
          },
        ],
        PBIPlayDayName: [{
            validator: function (rule, value, callback) {
              if (!value || value.length <= 0) {
                return callback(new Error(""));
              }
              if (that.baseInfo.PBIBelong == 3) {
                var re = /^([1-9][0-9]{0,10})$/;
                if (!re.test(value)) {
                  return callback(new Error("0"));
                }
              }
              callback();
            },
            trigger: "blur"
          },
          {
            min: 1,
            max: 20,
            message: "1-20",
            trigger: "blur"
          }
        ],
        PBISubTitle: [{
          validator: function (rule, value, callback) {
            if (!value || value.length <= 0) {
              return callback(new Error(""));
            }
            if (value.length > 50) {
              return callback(new Error("1-50"));
            }
            if (that.baseInfo.PBIBelong == 2) {
              var re = /^([\u4E00-\uFA29]|[\uE7C7-\uE7F3]|[a-zA-Z0-9 ]){0,60}$/;
              if (!re.test(value)) {
                return callback(new Error(""));
              }
            } else if (that.baseInfo.PBIBelong == 1) {
              if (that.baseInfo.CountryGarden != 1) {
                var subtitle = that.baseInfo.PBISubTitle;
                var newtitle = subtitle.replace(/[^\a-\z\A-\Z0-9\u4E00-\u9FA5\\\\]/g, "");
                if (newtitle != subtitle) {
                  return callback(new Error(""));
                }
              }
            }
            callback();
          },
          trigger: "blur,change"
        }],
        Departure: [{
          validator: function (rule, value, callback) {
            if (!value || value <= 0) {
              return callback(new Error(""));
            }
            callback();
          },
          trigger: "change"
        }],
        Destination: [{
          type: "array",
          required: true,
          message: "",
          trigger: "change"
        }],
      }
    };
  },
  computed: {
    /**
    
    */
    startlabel: function () {
      var that = this;
      if (that.baseInfo.PBIBelong == 1) {
        return "";
      } else {
        if (that.baseInfo.PBIProductType == 3) {
          return "";
        } else {
          return "";
        }
      }
    },
    /**
    
    */
    booklabel: function () {
      var that = this;
      if (that.baseInfo.PBIBelong == 1) {
        return "";
      } else {
        return "";
      }
    },
    /**
     * 
     */
    packageTypes: function () {
      var that = this;
      //that.baseInfo.PBIProductPackageType = 1;
      if (that.baseInfo.PBIBelong == 3) {
        return [{
          value: 1,
          text: ""
        }];
      } else if (that.baseInfo.PBIBelong == 2) {
        if (that.baseInfo.PBIProductType == 1) {
          return [{
              value: 1,
              text: ""
            },
            {
              value: 2,
              text: "+"
            }
          ];
        } else if (that.baseInfo.PBIProductType == 2) {
          return [{
              value: 1,
              text: ""
            },
            {
              value: 2,
              text: ""
            },
            {
              value: 3,
              text: ""
            }
          ];
        } else {
          return [{
            value: 1,
            text: ""
          }];
        }
      } else {
        return [{
          value: 1,
          text: ""
        }, {
          value: 2,
          text: ""
        }];
      }
    },
    /**
     * 
     */
    captions: function () {
      var that = this;
      if (that.baseInfo.PBIBelong == 3) {
        return [{
          value: 1,
          text: ""
        }];
      } else if (that.baseInfo.PBIBelong == 2) {
        if (that.baseInfo.PBIProductType == 2) {
          return [{
            value: 5,
            text: ""
          }];
        } else {
          if (that.borderTour == true) {
            return [{
              value: 4,
              text: ""
            }];
          } else {
            return [{
                value: 1,
                text: ""
              },
              {
                value: 2,
                text: ""
              },
              {
                value: 3,
                text: ""
              }
            ];
          }
        }
      } else {
        if (that.baseInfo.PBIProductType == 1) {
          return [{
              value: 1,
              text: ""
            },
            {
              value: 6,
              text: ""
            }
          ];
        } else if (that.baseInfo.PBIProductType == 2) {
          return [{
            value: 5,
            text: ""
          }, {
            value: 3,
            text: ""
          }];
        } else {
          return [{
              value: 1,
              text: ""
            },
            {
              value: 3,
              text: ""
            },
            {
              value: 6,
              text: ""
            },
            {
              value: 5,
              text: ""
            }
          ];
        }
      }
    },
    /**
     * true-false-
     */
    dynamicFreeLine: function () {
      var that = this;
      return (
        that.baseInfo.PBIBelong == 2 && that.baseInfo.PBIProductPackageType == 2
      );
    },
    /**
     *
     */
    multiresource: function () {
      var that = this;
      return (that.baseInfo.PBIBelong == 1 && that.baseInfo.PBIProductPackageType == 2);
    },
    /**
     * true-false-
     */
    isinner: function () {
      var that = this;
      return (
        that.baseInfo.PBIBelong == 3 ||
        (that.baseInfo.PBIBelong == 2 &&
          that.baseInfo.PBICaptionDescription != 4)
      );
    }
  },
  watch: {
    picselected: function (val) {
      var images = [];
      if (val && val.length >= 1) {
        val.forEach(function (item) {
          images.push({
            PIImageId: item.picid,
            PIImageName: item.picname,
            PIImageUrl: item.picurl,
            PIIsHead: item.picmain,
            ImageType: item.isvideo
          });
        });
      }
      this.baseInfo.Images = images;
    }
  },
  mounted: function () {
    this.initDatas();
  },
  methods: {
    onsupplierchange: function (val, item) {
      var that = this;
      if (item && item.IsBorderSupplier == 1) {
        that.borderTour = true;
        that.baseInfo.PBICaptionDescription = 4;
      } else {
        that.borderTour = false;
        that.baseInfo.PBICaptionDescription = 1;
      }
      that.baseInfo.PSISupplierContractName = "";
      that.baseInfo.PSISupplierContractWay = "";
      that.baseInfo.PSILinkId = 0;
      this.QueryProductSupplierInfo(val);
    },
    ondepturechange: function (val) {
      var that = this;
      if (that.baseInfo.PBIBelong == 3) {
        that.getProductManager(val);
      } else {
        that.getRelationCity(val);
      }
    },
    getProductManager: function (val) {
      var that = this;
      httputils
        .get(
          "/travel/supergoproduct/ProductRegionCharge/GetProductManagerList", {
            CityId: val,
            ProductAttribute: that.baseInfo.PBIProductAttribute
          }
        )
        .then(function (resp) {
          if (
            resp &&
            resp.ProductManagerList &&
            resp.ProductManagerList.length >= 1
          ) {
            that.baseInfo.PBIProductManagerTcNo = resp.ProductManagerList[0].ProductManagerTcNo;
            that.baseInfo.PBIProductManagerName = resp.ProductManagerList[0].ProductManagerName;
            that.productmanager = [{
              Value: resp.ProductManagerList[0].ProductManagerTcNo,
              Text: resp.ProductManagerList[0].ProductManagerName
            }];
          } else {
            that.productmanager = [];
            that.baseInfo.PBIProductManagerTcNo = "";
          }
        });
    },
    getRelationCity: function (val) {
      var that = this;
      if (!val || val <= 0) {
        that.bookDatas = [];
        that.baseInfo.RelationCity = [];
        return;
      }
      httputils
        .get("/travel/supergoproduct/ProductBaseInfo/GetProductRelationCity", {
          GoCityId: val,
          Belong: that.baseInfo.PBIBelong,
          ProductId: $("#productid").val()
        })
        .then(function (resp) {
          var selected = [];
          if (resp && resp.Result) {
            that.bookDatas = resp.Result;
            resp.Result.forEach(function (city) {
              if (city.IsChecked) {
                selected.push(city.GoCityId);
              }
            });
          } else {
            that.bookDatas = [];
          }
          that.baseInfo.RelationCity = selected;
        });
    },
    initDatas: function () {
      var that = this;
      var proid = $("#productid").val();
      var loading = that.$loading({
        lock: true
      });
      httputils
        .get("/travel/supergoproduct/ProductBaseInfo/GetBaseInfoJson", {
          proid: proid,
          belong: that.baseInfo.PBIBelong
        })
        .then(function (resp) {
          loading.close();
          if (resp) {
            var startcity = [],
              startid = "",
              endcity = [],
              endid = [],
              images = [];
            if (resp.CityListInfo) {
              resp.CityListInfo.forEach(function (city) {
                if (city.PCCityType == 4) {
                  startcity.push({
                    fullname: city.PCFullName,
                    name: city.PCCityName,
                    id: city.PCCityId
                  });
                  startid = city.PCCityId;
                } else if (city.PCCityType == 3) {
                  endcity.push({
                    fullname: city.PCFullName,
                    name: city.PCCityName,
                    id: city.PCCityId
                  });
                  endid.push(city.PCCityId);
                }
              });
              that.getRelationCity(startid);
            }

            if (resp.ImageListInfo) {
              resp.ImageListInfo.forEach(function (image) {
                images.push({
                  picid: image.PIImageId,
                  picname: image.PIImageName,
                  picurl: image.PIImageUrl,
                  picmain: image.PIIsHead,
                  isvideo: image.ImageType
                });
              });
            }
            resp.ProductBaseInfo.PBIProductType = resp.ProductBaseInfo.PBIProductType == -1 ? "" : resp.ProductBaseInfo.PBIProductType;
            resp.ProductBaseInfo.PBIProductPackageType = resp.ProductBaseInfo.PBIProductPackageType == -1 ? "" : resp.ProductBaseInfo.PBIProductPackageType;
            that.borderTour = resp.ProductBaseInfo.PBICaptionDescription == 4;
            var tagId = resp.ProductBaseInfo.PBITagId;
            var productattr = resp.ProductBaseInfo.PBIProductAttribute == -1 ? "" : resp.ProductBaseInfo.PBIProductAttribute;
            var includeguide = resp.ProductBaseInfo.PBIIncludeGuide;
            var operatetype = resp.ProductBaseInfo.PBIProductOperateType <= -1 ? "" : resp.ProductBaseInfo.PBIProductOperateType;
            var isHoliday = resp.ProductBaseInfo.PBIEIsHoliday;
            that.baseInfo = Object.assign(
              that.baseInfo,
              minux.checkBe(resp.ProductBaseInfo),
              minux.checkBe(resp.SupplierInfo), {
                Destination: endid.length >= 1 ? endid : [""],
                Departure: startid,
                Images: []
              }
            );

            that.baseInfo.PBIProductOperateType = operatetype;
            that.baseInfo.PBIProductAttribute = productattr;
            that.baseInfo.PBIEIsHoliday = isHoliday;
            that.baseInfo.PBITagId = tagId <= 0 ? "" : tagId;
            if (!that.baseInfo.PBIBelong != 3) {
              that.baseInfo.PBITagId = isHoliday;
            }
            that.picselected = images;
            that.destinationDatas = endcity;
            that.deptureDatas = startcity;
            that.baseInfo.PBIIncludeGuide = includeguide;
            if (resp.ProductBaseInfo) {
              that.QueryProductSupplierInfo(resp.SupplierInfo.PSISupplierId);
              that.productmanager = [{
                Value: resp.ProductBaseInfo.PBIProductManagerTcNo,
                Text: resp.ProductBaseInfo.PBIProductManagerName
              }];
              that.assistant = [{
                Value: resp.ProductBaseInfo.PBIAssistantNo || "",
                Text: resp.ProductBaseInfo.PBIAssistant || ""
              }];

              that.supplier = {
                SupplierId: resp.SupplierInfo.PSISupplierId,
                SupplierName: resp.SupplierInfo.PSISupplierName
              };
              that.localSupplier = {
                SupplierId: resp.SupplierInfo.PSILocalAgencySupplierId,
                SupplierName: resp.SupplierInfo.PSILocalAgencySupplierName
              };
            }
          }
        });
    },
    saveData: function () {
      var that = this;
      this.$refs["baseInfo"].validate(function (valid) {
        if (valid) {
          var loading = that.$loading({
            lock: true
          });
          httputils.post("/travel/supergoproduct/ProductBaseInfo/SaveProduct",
            that.baseInfo).then(resp => {
            loading && loading.close();
            if (resp && resp.IsSuccess) {
              that.$message({
                message: "",
                type: "success"
              });
              setTimeout(function () {
                window.location.href = '/travel/supergoproduct/GlProductInfo?Type=2&Id=' + resp.ProductId + '&OpeType=view';
              }, 1000);
            } else {
              that.$alert(resp.ErrorMsg, '', {
                dangerouslyUseHTMLString: true,
                type: "warning",
                center: true,
              });
            }
          }).catch(err => {
            that.$message({
              type: 'error',
              message: err
            });
            loading && loading.close();
          });
        } else {
          that.$confirm("", "", {
            confirmButtonText: "",
            type: "warning",
            center: true,
            showCancelButton: false
          });
        }
      });
      return;
    },
    getCityInfo(id) {
      var result = [];
      if (this.destinationDatas && this.destinationDatas.length >= 1) {
        result = this.destinationDatas.filter(function (item) {
          return item.id == id;
        });
      }
      return result;
    },
    addcity: function () {
      this.baseInfo.Destination.push("");
    },
    delcity: function (index) {
      this.baseInfo.Destination.splice(index, 1);
    },
    SupplierContractChange: function (val, item) {
      var that = this;
      $.each(this.SupplierContract, function (i, k) {
        if (k.Phone == val) {
          that.baseInfo.PSISupplierContractName = k.Name;
          that.baseInfo.PSILinkId = k.LinkmanId;
          return true;
        }
      })
    },
    QueryProductSupplierInfo: function (supplierId) {
      if (typeof (supplierId) == "undefined") {
        return;
      }
      var that = this;
      httputils
        .get(
          "/travel/supergoproduct/ProductSupplier/QueryProductSupplierInfo", {
            supplierId: supplierId
          }
        )
        .then(function (resp) {
          if (resp) {
            that.SupplierContract = resp;
            if (that.baseInfo.PSISupplierContractWay && resp && that.baseInfo.PSISupplierContractWay.length == 0 && resp.length > 0) {
              that.baseInfo.PSISupplierContractName = resp[0].Name;
              that.baseInfo.PSISupplierContractWay = resp[0].Phone;
              that.baseInfo.PSILinkId = resp[0].LinkmanId;
            }
          }
        });

    },
    producttypechange: function (item) {
      var that = this;
      if (that.baseInfo.PBIBelong == 3) {
        that.baseInfo.PBICaptionDescription = 1;
      } else if (that.baseInfo.PBIBelong == 2) {
        if (item == 2) {
          that.baseInfo.PBICaptionDescription = 5;
        } else {
          if (that.borderTour == true) {
            that.baseInfo.PBICaptionDescription = 4;
          } else {
            that.baseInfo.PBICaptionDescription = 1;
          }
        }
      } else {
        if (item == 1) {
          that.baseInfo.PBICaptionDescription = 1;
        } else if (item == 2) {
          that.baseInfo.PBICaptionDescription = 5;
        } else {
          that.baseInfo.PBICaptionDescription = 1;
        }
      }
    },
    productpackagetypechange: function (item) {
      if (this.baseInfo.PBIBelong == 1 && this.baseInfo.PBIOldProductId < 1) {
        if (item == 1) {
          this.baseInfo.ContainVisa = 1;
        } else {
          this.baseInfo.ContainVisa = 0;
        }
      }
    }
  }
});

$(function () {
  $("#saveProductInfo").click(function () {
    vue.saveData();
  });
});



