<template>
    <glui-select v-model="selected" :clearable="belong==3" placeholder="" no-match-text="" v-on:change="onchange">
        <glui-option v-for="item in datas" :key="item.value" :label="item.text" :value="item.value">
            <span style="float: left">{{ item.text }}</span>
        </glui-option>
    </glui-select>
</template>
<script>
import httputils from "@/js/httputils"
export default {
    data: function () {
        return {
            datas: [],
            selected: this.value
        };
    },
    props: {
        value: {
            type: [String, Number],
            default: function () {
                return "";
            }
        },
        belong: {
            type: [String, Number],
            default: function () {
                return 1;
            }
        }
    },
    watch: {
        value: function (val) {
            var that = this;
            if (that.belong == 3 && val == "0") {
                val = "";
            }
            that.selected = val.toString();
        },
        belong: function (val) {
            this.search();
        }
    },
    mounted: function () {
        this.search();
    },
    methods: {
        search: function () {
            var that = this;
            httputils
                .get("/travel/supergoproduct/GlTag/GetHolidayTags", {
                    belong: that.belong
                })
                .then(function (resp) {
                    if (resp) {
                        that.datas = resp;
                    } else {
                        that.datas = [];
                    }

                    that.$nextTick(function () {
                        if (that.belong == 3 && that.value == "0") {
                            that.selected = "";
                        } else {
                            that.selected = that.value;
                        }
                    });
                });
        },
        onchange: function (val) {
            this.$emit("input", val);
            var item = null;
            this.datas.forEach(function (opt) {
                if (opt && opt.value == val) {
                    item = opt;
                    return;
                }
            });
            this.$emit("change", val, item);
        }
    }
}
</script>




